@import url(https://fonts.googleapis.com/css2?family=Kalam:wght@700&family=Roboto:wght@300;400&display=swap);
* {
    padding:0;
    margin:0;
    box-sizing: border-box;
}
body {
    background-color: lavender;
    font-family: Roboto, sans-serif;
}


.Pokecard {
    background-color: white;
    width: 180px;
    height: 260px;
    text-align: center;
    border-radius: 10px;
    padding: 20px 2px;
    display:flex;
    flex-direction:column;
    justify-content: space-between;
    box-shadow: 5px 5px 10px 0px black;
    /* opacity: 0; */
    transition:transform 0.25s, opacity 0.5s;
}
.Pokecard:hover {
 transform:scale(1.07) translate(-2px, -2px);
 box-shadow: 10px 10px 15px -3px black;
}
.Pokecard:hover .Pokecard-img > img {
    transform: scale(1.15) translate(6px, 4px);
}
.Pokecard:hover .Pokecard-img {
    box-shadow: none;
    margin:0;
    border-radius:0px;
    height:120px;
}

.Pokecard-name {
    text-shadow: 4px 4px 2px rgba(114, 23, 0, 0.3);
    background: linear-gradient(to right,#2600ff 40%,  #ff009d 50%, #2600ff 60%);
    -webkit-background-clip: text;
            background-clip: text;
    font-family: Kalam;
    /* text-transform: uppercase; */
    font-size: 24px;
    letter-spacing: 1px;
    color: transparent;
    background-size: 165%;
    animation: gradient 11s alternate infinite cubic-bezier(0.34, 1.56, 0.64, 1);
}
@keyframes gradient {
    0% {
        background-position: 0%;
    }
    50% {
        background-position: 100%;
    }
    100% {
        background-position: 0%;
    }
}
.Pokecard-img {
    margin: 0 2%;
    height: 120px;
    background-color: rgba(255, 255, 255, 0.4);
    box-shadow: inset 0 1px 1px rgb(40, 0, 92);
    border-radius: 3px;
}

.Pokecard-img > img {
    width:70%;   
    transition: transform 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
}
.Pokecard-desc {
    color:#333;
}
.Pokecard-desc-type {
    margin: 4px;
}
.Pokedex-gameResult {
    max-width: 1100px;
    padding: 0 5%;
    margin: 0 auto; 
}

.Pokedex-container {
    max-width: 1100px;
    margin: 20px auto 40px;
    display:flex;
    justify-content: space-around;
}
.Pokedex-gameResult{
    font-weight:300;
}

.Pokedex-winner {
    font-weight:600;
color: rgb(1, 206, 1);
}
.Pokedex-loser {
    font-weight:600;
    color: rgb(255, 0, 98);
}
.Pokegame-title {
        text-align: center;
        font-size: 40px;
        letter-spacing: 5px;
        margin: 10px;
        font-family: Kalam, sans-serif;
}


